import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { Badge } from "react-bootstrap";
import UserList from "./UserList";
import dayjs from "dayjs";

import {
  UploadDiv,
  UploadButtonDiv,
  UploadForm,
} from "../../Style/UploadCSS.js";

import AddAd from "./AddAd";

function SecondaryStart() {
  let navigate = useNavigate();
  //  
  const user = useSelector(state => state.user);
  useEffect(() => {
    if (!user.accessToken) {
      alert("     ");
      navigate("/login");
    }
  });
  //  
  let params = useParams();
  const [Now, setNow] = useState(0);
  const [Info, setInfo] = useState([]);
  const [Care, setCare] = useState("");
  const [Multi, setMulti] = useState("");
  const [Start, setStart] = useState("");
  const [End, setEnd] = useState("");
  const [Num, setNum] = useState(0);
  const [Stat, setStat] = useState(false);
  const [Partner, setPartner] = useState("");
  const [Image, setImage] = useState("");

  const inputCare = e => {
    setCare(e.target.value);
  };

  const inputMulti = e => {
    setMulti(e.target.value);
  };

  useEffect(() => {
    let body = {
      com_code: params.com_code,
    };
    axios.post("/api/secondary/companydetail", body).then(response => {
      if (response.data.success) {
        setInfo(response.data.company);
      }
    });
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (Info.ad_start !== undefined) {
      setStart(Info.ad_start);
      setEnd(Info.ad_end);
      setCare(Info.care_use);
      setMulti(Info.multi_ad);
      setPartner(Info.partner);
      if (Start < Now < End) setStat(true);
      setNum(Info.ad_num);
    } else {
      setStart(" ...");
      setEnd("");
    }
    // eslint-disable-next-line
  }, [Info]);

  let date = dayjs();

  const onSubmit = e => {
    e.preventDefault();
    setNow(date.format("YYYYMMDD"));
    if (Start === "" || End === "") {
      return alert(",    ");
    }
    if (Now >= End) {
      return alert("  . ");
    }
    let body = {};
    if (Stat) {
      body = {
        start: Start,
        end: End,
        care: Care,
        multi: Multi,
        partner: Partner,
        ad_img: Image,
        num: Num,
        com_code: params.com_code,
      };
    } else {
      body = {
        start: Start,
        end: End,
        care: Care,
        multi: Multi,
        partner: Partner,
        num: Num + 1,
        com_code: params.com_code,
      };
    }

    axios
      .post("/api/secondary/start", body)
      .then(response => {
        if (response.data.success) {
          alert("!  ");
          navigate("/secondary");
        } else {
          alert(" !    ");
        }
      })
      .catch(err => {
        console.log(err);
      });
  };

  const codeHandler = e => {
    e.preventDefault();
    setPartner("");
  };
  return (
    <UploadDiv>
      <h4>
        {Info.com_name} {Info.com_area}
        <small
          style={{
            margin: "0 10px",
            fontWeight: "normal",
            color: "#666",
          }}
        >
             : {Num} 
        </small>
        {Stat ? (
          <Badge bg="success"></Badge>
        ) : (
          <Badge bg="secondary"></Badge>
        )}
      </h4>
      <UploadForm>
        <div className="row">
          <div className="form-group col-12 col-md-6">
            <label htmlFor="start">
              
              <small
                style={{
                  marginLeft: "10px",
                  fontWeight: "normal",
                  color: "#666",
                }}
              >
                8  (: 2022 10 20 - 20221020)
              </small>
            </label>
            <input
              type="text"
              id="start"
              value={Start}
              onChange={e => {
                setStart(e.currentTarget.value);
              }}
              className="form-control"
            ></input>
          </div>
          <div className="form-group col-12 col-md-6">
            <label htmlFor="end">
              
              <small
                style={{
                  marginLeft: "10px",
                  fontWeight: "normal",
                  color: "#666",
                }}
              >
                8  (: 2022 10 20 - 20221020)
              </small>
            </label>
            <input
              type="text"
              id="end"
              value={End}
              onChange={e => {
                setEnd(e.currentTarget.value);
              }}
              className="form-control"
            ></input>
          </div>
          <div className="form-group col-12 col-md-6">
            <label htmlFor="care"></label>
            <select
              id="care"
              className="form-select"
              onChange={inputCare}
              value={Care}
            >
              <option key="0" value="">
                 
              </option>
              <option key="1" value="">
                
              </option>
              <option key="2" value="">
                
              </option>
              <option key="3" value="+">
                +
              </option>
            </select>
          </div>
          <div className="form-group col-12 col-md-6">
            <label htmlFor="multi"></label>
            <select
              id="multi"
              className="form-select"
              onChange={inputMulti}
              value={Multi}
            >
              <option key="0" value="">
                
              </option>
              <option key="1" value="">
                
              </option>
              <option key="2" value="">
                
              </option>
              <option key="3" value="">
                
              </option>
            </select>
          </div>

          <div className="form-group col-12 col-md-6 col-lg-4">
            <label> </label>
            <AddAd setImage={setImage} comCode={params.com_code} />
          </div>

          <div className="form-group col-12 col-md-6 col-lg-6">
            <label htmlFor="com_code"></label>
            {Partner === "" ? (
              <UserList setPartner={setPartner} />
            ) : (
              <div className="input-group">
                <input
                  id="name"
                  type="text"
                  className="form-control"
                  value={Partner}
                  disabled
                  style={{ marginBottom: 0 }}
                />
                <button
                  className="btn btn-danger"
                  onClick={e => codeHandler(e)}
                >
                  
                </button>
              </div>
            )}
          </div>
        </div>
      </UploadForm>

      <UploadButtonDiv>
        <button
          className="cancel"
          onClick={e => {
            e.preventDefault();
            navigate(-1);
          }}
        >
          !
        </button>
        <button
          onClick={e => {
            onSubmit(e);
          }}
        >
          !
        </button>
      </UploadButtonDiv>
    </UploadDiv>
  );
}

export default SecondaryStart;
