import React, { useState, useEffect } from "react";
import LoginDiv from "../../Style/UserCSS";
import firebase from "../../firebase.js";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import Spinner from "react-bootstrap/Spinner";

function Register() {
  const [Name, setName] = useState("");
  const [Cell, setCell] = useState("");
  const [Email, setEmail] = useState("");
  const [PW, setPW] = useState("");
  const [PWChk, setPWChk] = useState("");
  const [Flag, setFlag] = useState(false);
  const [EmailChk, setEmailChk] = useState(false);
  const [EmailInfo, setEmailInfo] = useState("");

  let navigate = useNavigate();

  const user = useSelector(state => state.user);
  useEffect(() => {
    if (user.accessToken) {
      navigate(-1);
    }
  });

  const RegisterFunc = async e => {
    setFlag(true);
    e.preventDefault();
    if (!(Name && Email && PW && PWChk)) {
      return alert("   !");
    }
    if (PW !== PWChk) {
      return alert("   !");
    }
    if (PW.length < 8 || PWChk.length < 8) {
      return alert("  8   ");
    }
    if (!EmailChk) {
      return alert("   ");
    }
    if (Cell.includes("-")) {
      setCell(Cell.replaceAll("-", ""));
    }
    let createdUser = await firebase
      .auth()
      .createUserWithEmailAndPassword(Email, PW);

    await createdUser.user.updateProfile({
      displayName: Name,
      photoURL: 1,
    });
    let body = {
      email: createdUser.user.multiFactor.user.email,
      displayName: createdUser.user.multiFactor.user.displayName,
      uid: createdUser.user.multiFactor.user.uid,
      phone: Cell,
      photoURL: 1,
    };
    axios.post("/api/user/register", body).then(response => {
      if (response.data.success) {
        // 
        alert(" !   ");
        navigate("/login");
      } else {
        //
        alert(" !     ");
      }
      setFlag(false);
    });
  };

  const EmailCheckFunc = e => {
    e.preventDefault();
    if (!Email) {
      return alert("  ");
    }
    let body = {
      email: Email,
    };
    axios.post("/api/user/emailcheck", body).then(response => {
      if (response.data.success) {
        if (response.data.check) {
          setEmailChk(true);
          setEmailInfo("   ");
        } else {
          setEmailInfo("   ");
        }
      }
    });
  };

  const EmailUpdateFunc = e => {
    e.preventDefault();
    setEmailChk(false);
    alert("   ");
    setEmail("");
    setEmailInfo("");
  };

  return (
    <LoginDiv>
      <form>
        <label htmlFor="name"></label>
        <input
          type="text"
          id="name"
          value={Name}
          onChange={e => setName(e.currentTarget.value)}
        />

        <label htmlFor="cell"></label>
        <input
          type="text"
          id="cell"
          value={Cell}
          onChange={e => setCell(e.currentTarget.value)}
        />
        <label htmlFor="email"></label>
        <input
          type="text"
          id="email"
          value={Email}
          onChange={e => setEmail(e.currentTarget.value)}
          disabled={EmailChk}
        />
        {EmailChk ? (
          <button onClick={e => EmailUpdateFunc(e)}> </button>
        ) : (
          <button onClick={e => EmailCheckFunc(e)}> </button>
        )}
        <p className={EmailChk ? "currect" : "errMsg"}>{EmailInfo}</p>
        <label htmlFor="password"></label>
        <input
          type="password"
          id="password"
          value={PW}
          minLength={8}
          onChange={e => setPW(e.currentTarget.value)}
        />
        <label htmlFor="passwordChk"></label>
        <input
          type="password"
          id="passwordChk"
          value={PWChk}
          minLength={8}
          onChange={e => setPWChk(e.currentTarget.value)}
        />
        <button disabled={Flag} onClick={e => RegisterFunc(e)}>
          {Flag ? (
            <Spinner animation="border" role="status" size="sm" variant="light">
              <span className="visually-hidden">Loading...</span>
            </Spinner>
          ) : (
            <span></span>
          )}
        </button>
      </form>
    </LoginDiv>
  );
}

export default Register;
