import React, { useEffect, useState } from "react";
import { ApplyDiv, ApplyButtonDiv, ApplyForm } from "../../Style/ApplyCSS.js";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import ImageUpload from "../Post/ImageUpload.js";
import CompanyList from "./CompanyList";

function ApplyInput() {
  const user = useSelector(state => state.user);
  let navigate = useNavigate();

  const [Name, setName] = useState("");
  const [Birth, setBirth] = useState("");
  const [Phone, setPhone] = useState("");
  const [Memo, setMemo] = useState("");
  const [Image, setImage] = useState("");
  const [ComCode, setComCode] = useState("");
  const [Gender, setGender] = useState(0);
  const [Address, setAddress] = useState("");
  const [Path, setPath] = useState("");
  const [Career, setCareer] = useState("");
  const [Photo, setPhoto] = useState(false);

  const [CompanyName, setCompanyName] = useState("");

  const inputGender = e => {
    setGender(e.target.value);
  };
  const inputPath = e => {
    setPath(e.target.value);
  };
  const inputCareer = e => {
    setCareer(e.target.value);
  };

  const chkBirth = () => {
    if (Birth !== "") {
      if (Birth.length !== 2) {
        setBirth("");
        return alert("   ) 21 : 21");
      }
      if (Number(Birth) < 19) {
        setBirth("");
        return alert("  ");
      }
    }
  };

  useEffect(() => {
    if (!user.accessToken) {
      alert("     ");
      navigate("/login");
    }
    /*
    if (Number(user.photoURL) !== 0) {
      alert(" ,   ");
      navigate("/");
    }
    */
  });

  useEffect(() => {
    if (Image) {
      setPhoto(true);
    }
  }, [Image]);

  useEffect(() => {
    if (ComCode) {
      companySearch(ComCode);
    }
  }, [ComCode]);

  const companySearch = c => {
    let body = {
      searchTerm: c,
    };
    axios
      .post("/api/secondary/companysearch", body)
      .then(function (response) {
        if (response.data.success) {
          setCompanyName(
            response.data.companyName.com_name +
              " - " +
              response.data.companyName.com_area
          );
        }
      })
      .catch(function (error) {
        alert("");
        console.log(error);
      })
      .then(function () {});
  };

  const onSubmit = e => {
    e.preventDefault();
    let body = {
      name: Name,
      birth: Birth,
      phone: Phone,
      memo: Memo,
      image: Image,
      comCode: ComCode,
      gender: Gender,
      address: Address,
      photo: Photo,
      uid: user.uid,
      path: Path,
      career: Career,
      secondary: "",
    };
    axios
      .post("/api/apply/submit", body)
      .then(response => {
        if (response.data.success) {
          alert("!");
          navigate("/apply");
        } else {
          alert("!");
        }
      })
      .catch(err => {
        console.log(err);
      });
  };

  const codeHandler = e => {
    e.preventDefault();
    setComCode("");
  };

  return (
    <ApplyDiv className="container">
      <ApplyForm>
        <div className="row">
          <div className="form-group col-12 col-md-6 col-lg-6">
            <label htmlFor="com_code"></label>
            {ComCode === "" ? (
              <CompanyList setComCode={setComCode} />
            ) : (
              <div className="input-group">
                <input
                  id="name"
                  type="text"
                  className="form-control"
                  value={CompanyName}
                  disabled
                />
                <button
                  className="btn btn-danger"
                  onClick={e => codeHandler(e)}
                >
                  
                </button>
              </div>
            )}
          </div>
          <div className="form-group col-12 col-md-6 col-lg-6">
            <label htmlFor="path"></label>
            <select
              id="path"
              className="form-select"
              onChange={inputPath}
              value={Path}
            >
              <option key="" value="">
                 
              </option>
              <option key="0000" value="">
                
              </option>
              <option key="0001" value="()">
                ()
              </option>
              <option key="0002" value="()">
                ()
              </option>
              <option key="0003" value="()">
                ()
              </option>
              <option key="0004" value="()">
                ()
              </option>
              <option key="0005" value="()">
                ()
              </option>
              <option key="0006" value="()">
                ()
              </option>
              <option key="0007" value="()">
                ()
              </option>
              <option key="0008" value="()">
                ()
              </option>
              <option key="0009" value="()">
                ()
              </option>
              <option key="0010" value="">
                
              </option>
              <option key="0011" value="">
                
              </option>
              <option key="0012" value="()">
                ()
              </option>
            </select>
          </div>
          <div className="form-group col-12 col-md-6 col-lg-4">
            <label htmlFor="name"></label>
            <input
              id="name"
              type="text"
              className="form-control"
              value={Name}
              onChange={e => {
                setName(e.currentTarget.value);
              }}
            />
          </div>
          <div className="form-group col-12 col-md-6 col-lg-4">
            <label htmlFor="birth">
              
              <small
                style={{
                  marginLeft: "10px",
                  fontWeight: "normal",
                  color: "#666",
                }}
              >
                  () 21 = 21)
              </small>
            </label>
            <input
              id="birth"
              type="text"
              className="form-control"
              value={Birth}
              onChange={e => {
                setBirth(e.currentTarget.value);
              }}
              onBlur={e => {
                chkBirth();
              }}
            />
          </div>
          <div className="form-group col-12 col-md-6 col-lg-4">
            <label htmlFor="gender"></label>
            <select
              id="gender"
              className="form-select"
              onChange={inputGender}
              value={Gender}
            >
              <option key="2" value="2">
                  
              </option>
              <option key="0" value="0">
                
              </option>
              <option key="1" value="1">
                
              </option>
            </select>
          </div>
          <div className="form-group col-12 col-md-6 col-lg-4">
            <label htmlFor="address"></label>
            <input
              id="address"
              type="text"
              className="form-control"
              value={Address}
              onChange={e => {
                setAddress(e.currentTarget.value);
              }}
            />
          </div>
          <div className="form-group col-12 col-md-6 col-lg-4">
            <label htmlFor="phone"></label>
            <input
              id="phone"
              type="text"
              className="form-control"
              value={Phone}
              onChange={e => {
                setPhone(e.currentTarget.value);
              }}
            />
          </div>
          <div className="form-group col-12 col-md-6 col-lg-4">
            <label>()</label>
            <ImageUpload setImage={setImage} />
          </div>
        </div>
        <div className="row">
          <div className="form-group col-12 col-md-6">
            <label htmlFor="career"></label>
            <select
              id="career"
              className="form-select"
              onChange={inputCareer}
              value={Career}
            >
              <option key="2" value="-1">
                  
              </option>
              <option key="0" value="0">
                
              </option>
              <option key="1" value="1">
                
              </option>
            </select>
          </div>

          <div className="form-group col-12 col-md-6">
            <label htmlFor="career1"></label>
            {Career > 0 ? (
              <select
                id="career1"
                className="form-select"
                onChange={inputCareer}
                value={Career}
              >
                <option key="04" value="999">
                    
                </option>
                <option key="00" value="6">
                  
                </option>
                <option key="01" value="7">
                  
                </option>
                <option key="02" value="8">
                  TM
                </option>
                <option key="03" value="9">
                  
                </option>
              </select>
            ) : (
              <select
                id="career1"
                className="form-select"
                onChange={inputCareer}
                value={Career}
                disabled
              >
                <option key="4" value="999">
                    '' 
                </option>
                <option key="0" value="6">
                  
                </option>
                <option key="1" value="7">
                  
                </option>
                <option key="2" value="8">
                  TM
                </option>
                <option key="3" value="9">
                  
                </option>
              </select>
            )}
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="memo"></label>
          <textarea
            id="memo"
            value={Memo}
            className="form-control"
            onChange={e => {
              setMemo(e.currentTarget.value);
            }}
          />
        </div>
        <ApplyButtonDiv>
          <button
            onClick={e => {
              onSubmit(e);
            }}
          >
            !
          </button>
        </ApplyButtonDiv>
      </ApplyForm>
    </ApplyDiv>
  );
}

export default ApplyInput;
